<template>
  <div class="params">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
          <el-form-item label="销售组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" filterable @focus="companyListInvok">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.other_delivery_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" @change="departmentChange" placeholder="请选择" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="物流公司" size="mini">
            <el-select clearable v-model="params.logistics_id" @focus="logisticsInvok" @change="logisticsChange">
              <el-option v-for="item in logisticsList" :key="item.logistics_id" :label="item.logistics_name" :value="item.logistics_id">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="物流票号" size="mini">
            <el-input v-model="params.logistics_number" placeholder="请输入"></el-input>
          </el-form-item>

          <el-form-item label="运费结算方式" size="mini">
            <el-select clearable v-model="params.postage_type_id" @focus="PostageTypeListInvok" @change="postageTypeChange">
              <el-option v-for="item in postageTypeList" :key="item.postage_type_id" :label="item.postage_type_name" :value="item.postage_type_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="发货类型" size="mini">
            <el-select clearable v-model="params.delivery_type" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="三包出库发货" :value="1">三包出库发货</el-option>
              <el-option label="返胎出库发货" :value="2">返胎出库发货</el-option>
              <el-option label="旧胎退回发货" :value="3">旧胎退回发货</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="发货方式" size="mini">
            <el-select clearable v-model="params.send_mode" filterable>
              <el-option label="物流发货" :value="1">物流发货</el-option>
              <el-option label="客户自提" :value="2">客户自提</el-option>
              <el-option label="自提物流" :value="3">自提物流</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="司机" size="mini">
            <el-input v-model="params.driver_name" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="车牌号" size="mini">
            <el-input v-model="params.license_number" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini">
            <!-- 	是否显示打印次数为0的单据 -->
            <el-checkbox v-model="params.is_zero" false-label="0" true-label="1" name="type" style="margin:0px 22px 0 62px">仅显示打印次数为0的单据</el-checkbox>
          </el-form-item>

          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>

        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="audit('auditorPassOtherDelivery','审核')" size="mini" v-if="authority.b_pass==1">审核</el-button>
        <el-button icon="iconfont icon-zuofei" @click="audit('auditorInvalidOtherDelivery','作废')" size="mini" v-if="authority.b_invalid==1">作废</el-button>
        <el-button plain icon="iconfont icon-fanshenhe" @click="audit('unAuditorPassOtherDelivery','弃审')" size="mini" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button plain size="mini" icon="el-icon-printer" @click="printF(true)" v-if="authority.b_print==1">批量打印</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" :max-height="tableHeight" :summary-method="getSummaries" show-summary @selection-change="handleSelectionChange" v-loading="loading1" :row-style="selectRowChangeBg" @row-dblclick='examine(TableData.tableData,$event)'>
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="120">
            <template slot-scope="scope">
              <el-button @click="examine(scope, scope.row)" type="text" size="small" v-if="authority.b_info==1&&scope.row.is_auth==1">
                查看
              </el-button>
              <el-button disabled type="text" size="small" v-else>
                查看
              </el-button>
              <el-button @click="addF( scope.row)" type="text" size="small" v-if="scope.row.auditor_status==1 && authority.b_edit==1&&scope.row.is_auth==1">
                修改
              </el-button>
              <el-button disabled type="text" size="small" v-else>
                修改
              </el-button>

              <template v-if="scope.row.after_auditor_print_status == 1">
                <el-button @click="lodopDe(scope.row)" v-if="authority.b_print==1  && scope.row.auditor_status == 2 &&scope.row.is_auth==1 " type="text" size="small">
                  打印
                </el-button>
                <el-button disabled v-else type="text" size="small">
                  打印
                </el-button>
              </template>
              <template v-else>
                <el-button @click="lodopDe(scope.row)" v-if="authority.b_print==1 &&scope.row.is_auth==1" type="text" size="small">
                  打印
                </el-button>
                <el-button disabled v-else type="text" size="small">
                  打印
                </el-button>
              </template>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="销售组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="other_delivery_sn" width="156" label="单据号">
            <template slot-scope="scope">
              <a href="javaScript:;" @click="examine(scope,scope.row)">{{scope.row.other_delivery_sn}}</a>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="delivery_type" width="100" label="发货类型">
            <template slot-scope="scope">
              <span v-if="scope.row.delivery_type==1">三包出库发货</span>
              <span v-if="scope.row.delivery_type==2">返胎出库发货</span>
              <span v-if="scope.row.delivery_type==3">旧胎退回发货</span>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="88" label="业务日期">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="logistics_name" width="120" label="物流公司">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="logistics_number" width="120" label="物流票号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" align="right" property="postage_price" width="120" label="运费金额">
            <template slot-scope="scope">
              {{scope.row.postage_price | priceFormat}}
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="postage_type_name" width="88" label="运费结算方式">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="driver_name" width="120" label="司机">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="license_number" width="120" label="车牌号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="84" label="操作员">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_name" width="84" label="审核人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="70" label="审核状态">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="send_mode_name" width="160" label="发货方式">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="print_times" width="64" label="打印次数">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="140" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="140" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" min-width="130" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>
    <div v-if="printingStatusGet">
      <lodopDelivery ref="lodopDelivery" :title="title" @Search="onSearch"></lodopDelivery>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import {
  getData,
  opthionList,
  CompanySwitchClear,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
import lodopDelivery from "common/components/public/PublicPrint.vue"; //出库单打印组件

export default {
  name: "instorageOtherDeliveryOtherDeliveryIndex",
  data() {
    return {
      // authority: {},
      lock: 0,
      outerVisible: false,
      pages: 1,
      title: null,
      TableData: {
        tableData: [],
        allList: {},
        total: 10
      },
      timeValue2: null,
      params: {
        auditor_status: 0,
        delivery_type: 0
      },
      availableStocksFlag: 1,
      tableHeight: 600
    };
  },
  components: {
    lodopDelivery
  },
  computed: {
    ...mapGetters(["otherDeliveryIndexGet", "printingStatusGet"])
  },
  methods: {
    ...mapActions([
      "printingStatus",
      "otherDeliveryAddACT",
      "otherdeliveryIndexACT",
      "otherDeliverySeeACT"
    ]),
    getSummaries(param) {
      // 合并计算
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        if (index === 8) {
          sums[index] = this.TableData.allList.total_postage_price;
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },

    printF() {
      let companyFlag = false;
      let authFlag = false;
      const companyIds = this.multipleSelection;
      for (var i = 1; i < companyIds.length; i++) {
        if (companyIds[i - 1].company_id != companyIds[i].company_id) {
          companyFlag = true;
          break;
        }
      }
      for (var i = 0; i < companyIds.length; i++) {
        if (companyIds[i].is_auth == 0) {
          authFlag = true;
          break;
        }
      }
      if (!this.multipleSelection.length) {
        this.$message({
          message: "请选择单据进行打印",
          type: "warning"
        });
      } else if (companyFlag) {
        this.$message({
          message: "请选择相同公司单据进行打印",
          type: "warning"
        });
      } else if (authFlag) {
        this.$message({
          message: "已选择单据有个别公司未有打印权限",
          type: "warning"
        });
      } else {
        let flag = true,
          errorGoodsArr = [];
        this.multipleSelection.forEach(item => {
          if (item.after_auditor_print_status == 1) {
            //审核后才可打印
            if (item.auditor_status != 2) {
              errorGoodsArr.push(item.delivery_sn);
              flag = false;
            }
          }
        });

        if (flag) {
          this.multipleSelection.forEach(item => {
            this.lodopDe(item, true);
          });
        } else {
          let str = errorGoodsArr.map((item, i) => `${i + 1}.${item}`);
          this.$alert(
            `以下未审核的单据不可打印：<br/>
            <div style="color:#e6a23c">
              ${str.join("，")}
            <div>`,
            "提示",
            {
              dangerouslyUseHTMLString: true
            }
          );
        }
      }
    },
    lodopDe(row, index = 0) {
      this.printingStatus(true);
      this.$set(this, "title", "otherShipmentToPrint");
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf(
          {
            other_delivery_id: row.other_delivery_id
          },
          index
        );
      });
    },
    postageTypeChange(val) {
      if (val) {
        this.params.postage_type_name = this.postageTypeList.filter(
          obj => obj.postage_type_id === val
        )[0].postage_type_name;
      }
    },
    logisticsChange(val) {
      if (val) {
        this.params.logistics_name = this.logisticsList.filter(
          obj => obj.logistics_id === val
        )[0].logistics_name;
      }
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    selectRowChangeBg({ row, rowIndex }) {
      if (
        this.multipleSelection.length &&
        this.multipleSelection
          .map(obj => obj.other_delivery_id)
          .includes(row.other_delivery_id)
      ) {
        return "background:#d4e1ee";
      } else {
        return "";
      }
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }

      this.$set(this.params, "logistics_id", "");
      CompanySwitchClear(this, this.params);
    },
    logogram() {
      this.outerVisible = true;
    },
    audit(api, txt) {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: `请选择需要${txt}的信息`,
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].other_delivery_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "other_delivery_id",
        eachData: {
          other_delivery_id: rebateArr
        },
        examineApi: api,
        list: {
          UrlApi: "getOtherDeliveryList",
          data: this.TableData
        }
      };
      this.$common.examineFun(funcan);
    },
    addF(row) {
      // this.timeF();
      this.otherDeliveryAddACT({
        other_delivery_id: row ? row.other_delivery_id : null,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/inventory/otherdelivery/otherDeliveryAdd"
      });
    },
    onReset() {
      this.$set(this, "params", {
        is_zero: 0,
        auditor_status: 0,
        delivery_type: 0
      });
      // this.availableStocksFlag = 1;
      this.timeValue = [];
      this.timeValue2 = null;
      this.$set(this, "searchParams", {
        params: {
          // auditor_status: 0,
          // delivery_type: 0
        },
        TableData: this.TableData, //列表数据
        API: "getOtherDeliveryList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams, 2);
    },
    timeF() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      if (this.timeValue2 && this.timeValue2.length == 2) {
        this.params.auditor_start_time = this.timeValue2[0];
        this.params.auditor_end_time = this.timeValue2[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
    },
    onSearch() {
      // this.params.is_zero = this.availableStocksFlag ? 1 : 0;
      this.timeF();
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      this.otherdeliveryIndexACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
      getData(this.searchParams, 2);
    },
    //删除
    deletes() {
      var deledata = {
        deleID: "other_delivery_id",
        deleSelect: this.multipleSelection,
        deleApi: "delOtherDelivery",
        list: {
          UrlApi: "getOtherDeliveryList",
          data: this.TableData,
          esData: {
            page: this.pages
          }
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    examine(scope, row) {
      if (row.is_auth == 1) {
        this.otherDeliverySeeACT({
          other_delivery_id: row.other_delivery_id,
          params: this.params,
          goPage: this.goPage,
          pageSize: this.pageSize
        });
        this.$router.push({
          path: "/inventory/otherdelivery/otherDeliverySee"
        });
      }
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams, 2);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams, 2);
    }
  },
  mounted() {
    // this.authority = JSON.parse(localStorage.getItem("authority"));
    if (this.otherDeliveryIndexGet && this.otherDeliveryIndexGet.params) {
      if (this.otherDeliveryIndexGet.params.business_start_time) {
        const {
          business_start_time,
          business_end_time
        } = this.otherDeliveryIndexGet.params;
        this.timeValue = this.timeValue.concat([
          business_start_time,
          business_end_time
        ]);
      }
      this.$set(this, "params", this.otherDeliveryIndexGet.params);
      opthionList(this, this.otherDeliveryIndexGet.params);
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getOtherDeliveryList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams, 2, tableResize.bind(this));

    //取消单据右侧滚动条
    window.onresize = tableResize.bind(this);
  }
};
</script>

<style scoped>
</style>
